import React, { Component } from "react";
import mf from "diagram-library";
import cm from "mindfusion-common";
import "bootstrap/dist/css/bootstrap.min.css";
import {
  DiagramView,
  NodeListView,
  ZoomControl,
  Ruler
} from "diagram-library-react";
import MyAnimation from "./MyAnimation";

class DiagramApp extends Component {
  constructor(props) {
    super(props);

    var diagram = new mf.Diagramming.Diagram();
    diagram.setDefaultShape("Container");
    diagram.setLinkShape(mf.Diagramming.LinkShape.Spline);

    diagram.setTheme(mf.Diagramming.Theme.getBusiness());

    var effect = new mf.Diagramming.AeroEffect();
    effect.setOpacity(0.5);
    effect.setInnerOutlineColor("rgba(255,255,255,0.5)");
    // this.state.diagram.getNodeEffects().push(effect);

    //add the glass effect type 2
    var glassEffect = new mf.Diagramming.GlassEffect();
    glassEffect.setType(mf.Diagramming.GlassEffectType.Type2);
    glassEffect.setUsePenAsGlow(true);
    diagram.getNodeEffects().push(glassEffect);

    var pattern = new mf.Diagramming.AnchorPattern([
      new mf.Diagramming.AnchorPoint(50, 0, true, true),
      new mf.Diagramming.AnchorPoint(100, 50, true, true),
      new mf.Diagramming.AnchorPoint(50, 100, true, true),
      new mf.Diagramming.AnchorPoint(0, 25, true, true),
      new mf.Diagramming.AnchorPoint(0, 50, true, true)
    ]);

    var firstNode = diagram.getNodes()[0];
    var nodes = [];
    nodes.push(firstNode);

    var counter = 1;

    //this.arrangeDiagram(diagram);

    var shapeNodes = [];
    var shapeIds = ["Actor", "Plaque", "Database", "File"];

    /*var arrowHeadIds = [
      "Arrow",
      "Triangle",
      "Circle",
      "Tetragon",
      "Rhombus",
      "BowArrow",
      "PointerArrow",
      "Pentagon",
      "Reversed",
      "RevTriangle",
      "Quill",
      "RevWithLine",
      "RevWithCirc",
      "BackSlash",
      "Slash",
      "DefaultFlow",
      "DoubleArrow"
    ];*/
    var arrowHeadIds = ["Quill", "PointerArrow", "Pentagon"];
    var arrowHeadNames = ["Informations", "Energie", "Material"];

    var arrowHeadNodes = [];

    for (var i = 0; i < shapeIds.length; i++) {
      var node = new mf.Diagramming.ShapeNode(diagram);
      node.setShape(shapeIds[i]);
      shapeNodes.push(node);
    }

    for (var arrowHeadId of arrowHeadIds) {
      var arrowHead = new mf.Diagramming.ShapeNode(diagram);
      arrowHead.setShape(arrowHeadId);
      arrowHeadNodes.push(arrowHead);
    }
    //enable stting text field
    diagram.setAllowInplaceEdit(true);

    this.state = {
      diagram: diagram,
      nodes: shapeNodes,
      captions: shapeIds,
      pattern: pattern,
      arrowHeadShapes: arrowHeadNodes,
      arrowHeadIds: arrowHeadIds,
      arrowHeadNames: arrowHeadNames,
      behavior: mf.Diagramming.Behavior.LinkShapes
    };
  }
  onSelectedLinkChanged(sender, args) {
    var selectedShape = args.getNode().getShape();
    this.state.diagram.setLinkHeadShape(selectedShape);
  }

  arrangeDiagram(diagram) {
    var layout = new mf.Graphs.SpringLayout();
    //layout.direction = mf.Graphs.LayoutDirection.LeftToRight;
    // diagram.arrange(layout);
    layout.iteration = 100;
    this.state.diagram.arrange(layout);
    this.state.diagram.resizeToFitItems();
    // this.state.diagram.;
  }

  onSelectChange(event) {
    this.setState({ behavior: +event.target.value });
  }

  onSelectedLinkTypeChanged(event) {
    this.state.diagram.setLinkShape(event.target.val);

    if (event.target.value == 2) this.state.diagram.setRouteLinks(true);
    else this.state.diagram.setRouteLinks(false);
  }

  onLockClicked(event) {
    var diagram = this.state.diagram;
    var selectedItems = diagram.getSelection().nodes;
    var listChilds = [];
    if (selectedItems.length > 0) {
      var selectedNode = selectedItems[0];

      /*  if (
        selectedNode.getEnabledHandles() !==
        mf.Diagramming.AdjustmentHandles.None
      )
        selectedNode.setEnabledHandles(mf.Diagramming.AdjustmentHandles.None);
      else selectedNode.setEnabledHandles(mf.Diagramming.AdjustmentHandles.All);
    }*/
      listChilds = selectedNode.getChildren();

      if (listChilds.length > 0 && listChilds[0].getVisible()) {
        listChilds.forEach((item) => {
          item.setVisible(false);
        });
      } else if (listChilds.length > 0) {
        listChilds.forEach((item) => {
          item.setVisible(true);
        });
      } else {
        return;
      }
    }
  }

  onChange(e) {
    let files = e.target.files;
    console.log(files);
    let reader = new FileReader();
    reader.readAsText(files[0]);
    reader.onload = (e) => {
      const file = e.target.result;
      const data = JSON.parse(file);

      const functions = data.functions;
      var alreadyDrawnChild = [];
      var containers = [];

      for (var i = 0; i < functions.length; i++) {
        if (!alreadyDrawnChild.find((id) => id === functions[i].id)) {
          var container = this.state.diagram
            .getFactory()
            .createContainerNode(new cm.Drawing.Rect(10, 20, 50, 25));

          container.setText(functions[i].name);

          container.setAnchorPattern(this.state.pattern);
          containers.push({ id: functions[i].id, container: container });

          if (functions[i].childs.length > 0) {
            // loop throuth all childs
            for (var j = 0; j < functions[i].childs.length; j++) {
              var childContainer = this.state.diagram
                .getFactory()
                .createContainerNode(new cm.Drawing.Rect(10, 20, 25, 10));
              alreadyDrawnChild.push(functions[i].childs[j]);
              var child = functions.find(
                (fun) => fun.id === functions[i].childs[j]
              );
              if (child) {
                containers.push({ id: child.id, container: childContainer });
                childContainer.setText(child.name);
                childContainer.setAnchorPattern(this.state.pattern);
                container.add(childContainer);
              }
            }
            container.resizeToFitChildren(true, 2);
            container.arrange(new mf.Graphs.LayeredLayout());
          }
        } else if (functions[i].childs.length > 0) {
          for (var j = 0; j < functions[i].childs.length; j++) {
            var childContainer = this.state.diagram
              .getFactory()
              .createContainerNode(new cm.Drawing.Rect(10, 20, 25, 10));
            alreadyDrawnChild.push(functions[i].childs[0]);
            var child = functions.find(
              (fun) => fun.id === functions[i].childs[0]
            );

            if (child) {
              containers.push({ id: child.id, container: childContainer });
              childContainer.setText(child.name);
              childContainer.setAnchorPattern(this.state.pattern);
            }
          }
          var fatherContainer = containers.find(
            (c) => c.id === functions[i].id
          );
          fatherContainer.container.add(childContainer);
          fatherContainer.container.resizeToFitChildren(true, 2);
          fatherContainer.container.arrange(new mf.Graphs.LayeredLayout());
        }
      }
      //customize the first node
      const firstNode = containers.find((c) => c.id === 1);
      firstNode.container.setBrush({
        type: "SolidBrush",
        color: "rgb(255, 212, 50)"
      });

      //draw links
      for (var i = 0; i < data.links.length; i++) {
        var idOrigin = data.links[i].origin;
        var idTarget = data.links[i].target;

        // create a link
        var origin = containers.find((c) => c.id === idOrigin);
        var target = containers.find((c) => c.id === idTarget);
        var link = new mf.Diagramming.DiagramLink(
          this.state.diagram,
          origin.container,
          target.container
        );
        link.setShape(mf.Diagramming.LinkShape.Cascading);
        link.setHeadBrush("blue");

        // var linkStyle = new mf.Diagramming.Style();
        // linkStyle.setBrush({type:'SolidBrush', color:'rgb(255, 0, 5)'});
        // linkStyle.setStroke("#959595");
        // this.state.diagram.getTheme().styles['std:DiagramLink'] = linkStyle;

        link.setHeadShape("Triangle");
        link.setHeadShapeSize(3);
        this.state.diagram.addItem(link);
      }

      //Styling
      this.arrangeDiagram(this.state.diagram);

      //style links

      // var linkStyle = new mf.Diagramming.Style();
      // linkStyle.setBrush({type:'SolidBrush', color: 'rgb(255, 0, 5)'});
      // linkStyle.setStroke('#959595');
      // this.state.diagram.getTheme().styles['std:DiagramLink'] = linkStyle;
    };
  }
  render() {
    var props = {
      id: "diagram1",
      backBrush: "#f2f2f2",
      behavior: this.state.behavior
    };
    return (
      <div class="container">
        <div class="row " style={{ backgroundColor: "#FF7600" }}>
          <div class="p-3 col-sm-5">
            <img src="samares-logo.png" alt="Trulli" width="200" height="100" />
          </div>

          <div class="p-3 col-sm-3 ">
            <input type="file" onChange={(e) => this.onChange(e)} />
            <button
              id="lockItem"
              style={{ marginTop: "10px" }}
              onClick={this.onLockClicked.bind(this)}
            >
              Lock/Unlock
            </button>
            <br />
            <button
              style={{ marginTop: "10px" }}
              id="lockItem"
              onClick={this.onLockClicked.bind(this)}
            >
              Open/Close
            </button>
          </div>
          <div class="p-3 col-sm-4 ">
            <label for="Behavior">Behavior </label>
            <select
              class="form-select"
              defaultValue="3"
              onChange={this.onSelectChange.bind(this)}
            >
              <option value="3">LinkFunctions</option>
              <option value="10">DrawFunctions</option>
              <option value="5">DrawTable</option>
              <option value="0">Modify</option>
              <option value="12">Pan</option>
            </select>
            <label for="linkType">Link Type </label>
            <select
              class="form-select"
              id="linkType"
              defaultValue="3"
              onChange={this.onSelectedLinkTypeChanged.bind(this)}
            >
              <option value="2">Cascading</option>
              <option value="0">Bezir</option>
              <option value="3">Spline</option>
              <option value="1">Polyline</option>
            </select>
          </div>
        </div>
        <div class="row mt-1  ">
          <div class="col-sm-2 bg-light border border-dark rounded-right mt-5">
            <label for="Nodes"> Nodes </label>
            <NodeListView
              style={{ width: "100px" }}
              nodes={this.state.nodes}
              captions={this.state.captions}
            ></NodeListView>
            <label for="Nodes"> ArrowShapes </label>
            <NodeListView
              onNodeSelected={(sender, args) =>
                this.onSelectedLinkChanged(sender, args)
              }
              // style={{ height: "800px" }}
              nodes={this.state.arrowHeadShapes}
              captions={this.state.arrowHeadNames}
            ></NodeListView>
          </div>
          <div class=" col-sm-9 bg-light rounded border border-dark ">
            <DiagramView
              diagram={this.state.diagram}
              {...props}
              style={{ height: "600px " }}
            />
          </div>
          <div class=" col-sm-1 bg-light ">
            <ZoomControl diagram={this.state.diagram}></ZoomControl>
          </div>
        </div>
        <div class="row">
          <div class="col-sm-2 bg-light " />
          <div class=" col-sm-9 bg-light">
            <MyAnimation />
          </div>
          <div class="col-sm-1 bg-light " />
        </div>
      </div>
    );
  }
}

export default DiagramApp;
